import React, { useContext } from "react"
import ThemeContext from "../utils/theme"
import { Navbar, Nav, Form, Image } from "react-bootstrap"
import { Link } from "gatsby"
import "./Fontawesome.js"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

import { loggedUser } from  "./FirebaseAuth"

export default () => {
  const { dark, toggleDark, toString } = useContext(ThemeContext)
  let currentUser = loggedUser()
  return (
    <Navbar variant={toString()} fixed="top" collapseOnSelect expand="md" style={{paddingLeft:"1rem",paddingRight:"1rem"}}>
      <Navbar.Brand className="pl-5 " as={Link} to="/">
        {
        // <FontAwesomeIcon
        //   icon={["fab", `${dark ? "empire" : "rebel"}`]}
        //   className={`brand-icon ${dark ? "empire" : "rebel"}`}
        //   title="Home"
        // />
        }
        <Image
          width="150"
          height="150"
          fluid
          src={dark ? `../../icons/Appraisir-Logo-White.png` : `../../icons/Appraisir-Logo.png`}
          alt={"Appraisir"}
        />
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse
        id="responsive-navbar-nav"
        className="justify-content-end"
      >
        <Nav className="pr-3 mr-4 nav-links">
          {
          // <Nav.Link className="ml-2" as={Link} to="/blog" title="Blog">
          //   Blog
          // </Nav.Link>
          // <Nav.Link className="ml-2" as={Link} to="/about" title="About">
          //   About
          // </Nav.Link>
          // <Nav.Link className="ml-2" as={Link} to="/projects" title="Projects">
          //   Projects
          // </Nav.Link>
          // <Nav.Link className="ml-2" as={Link} to="/resume" title="Resume">
          //   Resume
          // </Nav.Link>
          }
          <Nav.Link className="ml-2" as={Link} to="/" title="Home">
            Home
          </Nav.Link>
          <Nav.Link className="ml-2" as={Link} to="/About" title="About">
            About
          </Nav.Link>
          <Nav.Link className="ml-2" as={Link} to={currentUser ?  "/Dashboard" : "/Login"} title={currentUser ?  "Dashboard" : "Login"}>
            {currentUser ?  "Profile" : "Log In"}
          </Nav.Link>
          <Form className="ml-3 my-auto">
            <Form.Check
              type="switch"
              id="custom-switch"
              label=""
              title="Toggle Theme"
              checked={dark}
              onChange={toggleDark}
            />
          </Form>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}
